<template>
        <el-table
                    :data="tableData"
                    style="width: 100%">
                    <el-table-column
                    label="头像"
                    width="180">
                    <template slot-scope="scope">
                        <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="姓名"
                    width="180">
                    <template slot-scope="scope">
                        <el-popover trigger="hover" placement="top">
                        <p>姓名: {{ scope.row.username }}</p>
                        <p>住址: {{ scope.row.password }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.username}}</el-tag>
                        </div>
                        </el-popover>
                    </template>
                    </el-table-column>
                    <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        type="danger"
                        @click="handleEdit(scope.$index, tableData)">取消关注</el-button>
                    </template>
                    </el-table-column>
                </el-table>
</template>
<script>
import axios from 'axios'
export default{
    mounted(){
        //该请求表示向后端请求该用户关注的所有用户数据
        axios.get(`http://localhost:3000/toconcern?username=`+localStorage.name).then((res)=>{
           this.tableData=res.data
        })
    },data(){
        return {
            tableData: []
        }
    },methods:{
        //由于时间关系，这里只做了客户端取消关注的程序，后端并不能取消关注。
        handleEdit(index, mytable) {
            mytable.splice(index,1)
      },
    }
}
</script>