<template>
  <div class="updatePassWord">
    <div class="updatePassWordAll">  
        <div class="enrolltips">修改密码</div>    
<div class="enroll_top">       
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item  prop="account">
    <el-input v-model="ruleForm.account" placeholder="账号"></el-input>
  </el-form-item>
        <el-form-item  prop="pass">
    <el-input type="password" v-model="ruleForm.pass" autocomplete="off" placeholder="请输入新密码"></el-input>
  </el-form-item>
  <el-form-item  prop="checkPass">
    <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off" placeholder="确认新密码"></el-input>
  </el-form-item>
  <div class="forget">
        </div>
        <br>
  <el-form-item class="enroll_bottom">
    <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
    <el-button @click="resetForm('ruleForm')">重置</el-button>
  </el-form-item>
</el-form>
     </div>
    </div>
  </div>
</template>
<script>
    import axios from 'axios';
    export default {
      data() {
        var checkaccount = (rule, value, callback) => {
          if (!value) {
            return callback(new Error('账号不能为空'));
          }
          callback();
        };
        var validatePass = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('请输入密码'));
          } else {
            if (this.ruleForm.checkPass !== '') {
              this.$refs.ruleForm.validateField('checkPass');
            }
            callback();
          }
        };
        var validatePass2 = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('请再次输入密码'));
          } else if (value !== this.ruleForm.pass) {
            callback(new Error('两次输入密码不一致!'));
          } else {
            callback();
          }
        };
        return {
          ruleForm: {
            pass: '',
            checkPass: '',
            account: ''
          },
          rules: {
            pass: [
              { validator: validatePass, trigger: 'blur' }
            ],
            checkPass: [
              { validator: validatePass2, trigger: 'blur' }
            ],
            account: [
              { validator: checkaccount, trigger: 'blur' }
            ]
          }
        };
      },
      methods: {
        submitForm(formName) {
            axios.get(`http://localhost:3000/updatePassWord?currentName=${this.ruleForm.account}`).then((res)=>{
                try{            
                    if(res.data.username==this.ruleForm.account)
                {            
               axios.get(`http://localhost:3000/updatePassWord?newpassword=${this.ruleForm.pass}&currentName=${this.ruleForm.account}`).then(async(res)=>{
                    console.log("修改成功");
                   await this.$message({
                    message: '密码修改成功,请重新登录',
                    type: 'success'
                    });
                    setTimeout(()=>{
                    location.href="http://localhost:3000/?num=log"
                    },1000)    
                 })               
                }
                else{
                    this.$message.error('抱歉，没有找到该用户');
                    }
                }catch(err){
                    this.$message.error('抱歉，没有找到该用户');
                }
                })
        },
        resetForm(formName) {
          this.$refs[formName].resetFields();
        }
      }
    }
  </script>
  
<style scoped>
    .updatePassWord{width:100%;height: 100%;display: flex;justify-content:center;align-items:center}
.updatePassWordAll{width:350px;height:400px;display: flex;justify-content:center;flex-direction: column;align-items:center;background: white;}
    .passwordInput,.nameInput{width:250px}
.enroll_top{display: flex;flex-direction: column;align-items:center;height: 270px;}
.enroll_bottom{width: 250px;}
.demo-ruleForm{margin-left: -60px;}
.enrolltips{margin-bottom: 70px;}
.forget{display: flex;justify-content:space-between;width: 250px;margin-left: 140px;}
.forget a{
    text-decoration: none;
    color: rgb(182, 182, 182);
}
/* 状态二: 已经访问过的链接 */
.forget a:visited {
    text-decoration: none;
    color: rgb(182, 182, 182);
}
/* 状态三: 鼠标划过(停留)的链接(默认红色) */
.forget a:hover {
    text-decoration: none;
    color:red;
}
/* 状态四: 被点击的链接 */
.forget a:active {
    text-decoration: none;
    color: red
}
</style>