<template>
    <div >
        <!-- 动态加载组件，通过change值来改变需要加载的组件，默认加载successLog组件，
        这里的changeValue是successLog组件点击关注用户按钮后传值：followTheUser，然后这里接收该值
    并将successLog组件改为followTheUser组件 -->
        <component :is="change" @changeValue="myChangeValue"></component>
    </div>
</template>
<script>
//依次引入登录成功组件和关注用户组件
import successLog from '@/views/successLog.vue'
import followTheUser from '@/views/followTheUser.vue'
export default{
    data(){
        return{
            change:"successLog"
        }
    },components:{
        successLog,followTheUser
    },methods:{
        myChangeValue(data){
           this.change=data
        }
    }
   
}
</script>
<style scoped>
div{height: 100%;}
</style>