<template>
    <div class="mylog">
    <div class="myLogAll">
        <div>欢迎登录</div>
        <el-tabs v-model="activeName" @tab-click="handleClick" class="mylog_content">
    <el-tab-pane label="密码登录" name="first" >
        <div class="tips el-icon-info" v-show="tipsShow">抱歉亲！用户或密码错误</div>
        <div class="mylog_top">
       <div>
        <el-input  placeholder="请输入姓名"  v-model="name"  clearable class="nameInput" prefix-icon="el-icon-user-solid"></el-input>
       </div>
       <br>
		<div>
        <el-input placeholder="请输入密码" v-model="password" show-password class="passwordInput" prefix-icon="el-icon-lock"></el-input>
        </div>        
        <div class="forget">
            <p><a href="http://localhost:3000/?num=forgetPassWord">忘记密码</a></p>
            <p><a href="http://localhost:3000/?num=enroll">免费注册</a></p>
        </div>    
        <el-row>
            <el-button type="danger" plain @click="log()">登录</el-button>
        </el-row>
     </div>
    </el-tab-pane>
    <el-tab-pane label="短信登录" name="second">
        <div class="tips el-icon-info" v-show="tipsShow">抱歉亲！手机号或验证码错误</div>
        <div class="mylog_top">
       <div>
        <el-input  placeholder="请输入手机号" v-model="name"  clearable class="nameInput" prefix-icon="el-icon-mobile-phone"></el-input>
       </div>
       <br>
		<div>
        <el-input placeholder="请输入验证码"   class="passwordInput" prefix-icon="el-icon-chat-round" >
            <template slot="append">获取验证码</template>
        </el-input>
        </div>        
        <div class="forget">
            <p><a href="http://localhost:3000/?num=forgetPassWord">忘记密码</a></p>
            <p><a href="http://localhost:3000/?num=enroll">免费注册</a></p>
        </div>       
        <el-row>
            <el-button type="danger" plain @click="log()">登录</el-button>
        </el-row>
     </div>
    </el-tab-pane>
  </el-tabs>
  <p>or</p>
  <div class="mylog_bottom">
    <a href="#"><img src="@/assets/weixin.png"></a>
    <a href="#"><img src="@/assets/qq.png"></a>
  </div>
  <div class="mylog_bottom">
  </div>
    </div>
   </div>
</template>
<script>
    import axios from 'axios';
export default{
    data(){return{
			name:null,password:null, activeName: 'second',tipsShow:false
		}},methods:{
            log(){
            //通过后端的getUserInfo API里保存的用户信息与当前用户输入的信息进行比较，判断该用户是否存在
			 axios.get(`http://localhost:3000/getUserInfo?myname=${this.name}`).then((res)=>{
                try{
                    if(res.data.username==this.name&&res.data.password==this.password)
                {
                    console.log("登录成功");
                     localStorage.name=this.name;               
                    localStorage.log=0;
                      location.href="http://localhost:3000";             
                }
                else{
                    this.tipsShow=true;}
                }catch(err){
                    this.tipsShow=true;
                }
                })
		},  handleClick(tab, event) {
      }
    },mounted(){
        localStorage.log=-1
    }
}
</script>
<style scoped>
.mylog{width:100%;height:100%;display: flex;justify-content:center;align-items:center}
.myLogAll{width:350px;height: 450px;background: white;display: flex;justify-content:center;flex-direction: column;align-items:center}
.passwordInput,.nameInput{width:250px}
.mylog_content{width: 250px;height: 270px;background: rgb(255, 255, 255);margin-top: 20px;}
.mylog_top{display: flex;flex-direction: column;align-items:center}
.forget{display: flex;justify-content:space-between;width: 250px;}
img{width: 30px;height: 30px;}
.mylog_bottom{width: 250px;display: flex;justify-content: space-around;}
.el-icon-info{width: 250px;margin: 0 auto 15px auto;color: red;text-align: center;}
.forget a{
    text-decoration: none;
    color: rgb(182, 182, 182);
}
/* 状态二: 已经访问过的链接 */
.forget a:visited {
    text-decoration: none;
    color: rgb(182, 182, 182);
}
/* 状态三: 鼠标划过(停留)的链接(默认红色) */
.forget a:hover {
    text-decoration: none;
    color:red;
}
/* 状态四: 被点击的链接 */
.forget a:active {
    text-decoration: none;
    color: red
}
.clickClass{color: red;}
</style>