<template>
    <div class="successLog">
        <el-row class="myelrow">
  <el-col :sm="12" :lg="6" class="myelcol">
    <el-result icon="success" title="亲爱的用户" subTitle="您已成功登录">
      <template slot="extra">
        <el-button type="danger" size="medium" @click="followTheUser()" plain style="margin:0 0 10px 10px">关注用户</el-button>
        <el-button type="primary" size="medium" @click="back()">退出登录</el-button>
      </template>
    </el-result>
  </el-col>
  </el-row>
    </div>
</template>
<script>
export default{
    data(){
        return{
            username:""
        }
    },methods:{
      //back函数表示退出当前登录状态，并跳转到log组件进行用户登录
        back(){
           localStorage.log=1;
           location.href="http://localhost:3000?num=log"
        },followTheUser(){
          this.$emit("changeValue","followTheUser")
        }
    },mounted(){
      history.pushState(null, null, document.URL);
   window.addEventListener("popstate",function(e) {  
        history.pushState(null, null, document.URL);
    }, false);
    }
}
</script>
<style scoped>
.myelrow,.myelcol{
    background: white;width:300px;height:350px;
}
.successLog{width:100%;height: 100%;display: flex;justify-content:center;align-items:center}
.myelcol{display: flex;justify-content: center;}
</style>