<template>
  <div class="followTheUserAll">
    <div class="followTheUser">
      <el-button type="danger" @click="goBack()">返回</el-button>
        <el-tabs type="border-card">
          <!-- 推荐关注即浏览器通过后端的followtheuser API获取所有的用户信息，在客户端将所有用户信息渲染出来（这里只是做了一个简单的用户显示包括姓名，头像） -->
            <el-tab-pane label="推荐关注">
                <el-table
                    :data="tableData"
                    style="width: 100%">
                    <el-table-column
                    label="头像"
                    width="180">
                    <template slot-scope="scope">
                        <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="姓名"
                    width="180">
                    <template slot-scope="scope">
                        <el-popover trigger="hover" placement="top">
                        <p>姓名: {{ scope.row.username }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.username}}</el-tag>
                        </div>
                        </el-popover>
                    </template>
                    </el-table-column>
                    <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        type="danger"
                        :disabled="scope.row.isConcerned"
                        @click="handleEdit(scope.$index, scope.row)">{{scope.row.isConcernedValue}}</el-button>
                    </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <!-- 我的关注即当前用户关注的用户，通过后端的toconcern API接口获取到当前用户关注的其他用户，并在客户端渲染信息 -->
            <el-tab-pane label="我的关注">
              <!-- 这里加载当前用户关注的所有用户信息 -->
               <myConcern :key="concernData.length"></myConcern>
            </el-tab-pane>
        </el-tabs>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import myConcern from '@/views/myConcern.vue'
export default{
  mounted(){
    //禁止浏览器后退，因为是动态加载组件，如果用户鼠标后退将可能跳转到登录页面，造成用户体验不佳，因此这里禁止浏览器后退。
    //选择了添加返回按钮回到首页
    history.pushState(null, null, document.URL);
   window.addEventListener("popstate",function(e) {  
        history.pushState(null, null, document.URL);
    }, false);
    axios.get("http://localhost:3000/followtheuser").then((res)=>{
       this.tableData=res.data;
    })
  },data() {
      return {
        tableData: [],concernData:[]
      }
    }, methods: {
      //用户点击关注按钮就会将被点击的其他用户id传入到该用户的myconcern数组中（具体操作后端实现）
      async handleEdit(index, row) {
        row.isConcernedValue="已关注"
        row.isConcerned=true
      await  axios.get(`http://localhost:3000/toconcern?myConsernUserId=${row.userid}&username=`+localStorage.name).
      then((res)=>{
        this.concernData=res.data
      })
   
      },
      handleDelete(index, row) {
        console.log(index, row);
      },goBack(){
        location.reload()
      }
      
    },components:{myConcern}
}
</script>
<style scoped>
.followTheUser{width:1200px;margin:0 auto 0 auto;padding-top: 20px;}
:deep .cell{width: 100px;}
.followTheUserAll{width: 100%;height: 100%;}
</style>