<template>
	<div class="logging">
   <!-- 动态加载组件，通过改变change值改变首先需要显示的组件 -->
		<component :is="change"></component>
	</div>
</template>
<script type="text/javascript">
// 依次引入登录，注册，完成注册，尚未登录和忘记密码组件
	import log from '@/views/log.vue'
	import enroll from '@/views/enroll.vue'
	import completeLog from '@/views/completeLog.vue'
	import toLog from '@/views/toLog.vue'	
	import forgetPassWord from '@/views/forgetPassWord.vue'
	export default{
		components:{log,enroll,completeLog,forgetPassWord,toLog},beforeMount(){
			//通过获取客户端路由参数num的值来加载对应的路由组件
			let searchParams=new URLSearchParams(location.search)
			if(searchParams.get("num")!=null){
				this.change =searchParams.get("num")
			}
			//如果用户未登录，首页默认加载toLog组件	
			else{this.change="toLog"}		
		},data(){
			return{
				change:""
			}	
		},methods:{
		},mounted(){
	//本地保存的localStorage.log的值表示用户登录状态，0表示已经登录，其他值表示未登录		
			if(localStorage.log==0){
				this.change=completeLog
			}
		}
	}
</script>
