<template>
    <div class="myenroll">
    <div class="myEnrollAll">  
        <div class="enrolltips">欢迎注册</div>    
         <div class="enroll_top">       
           <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item  prop="account">
              <el-input v-model="ruleForm.account" placeholder="账号"></el-input>
            </el-form-item>
           <el-form-item  prop="pass">
            <el-input type="password" v-model="ruleForm.pass" autocomplete="off" placeholder="密码"></el-input>
           </el-form-item>
           <el-form-item  prop="checkPass">
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off" placeholder="确认密码"></el-input>
           </el-form-item>
         <div class="forget">
          <!-- 跳转到登录界面 -->
            <span ><a href="http://localhost:3000/?num=log">我已注册，立即登录</a></span>
        </div>
        <br>
          <el-form-item class="enroll_bottom">
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
       </el-form>
     </div>
    </div>
  </div>
</template>
<script>
    import axios from 'axios';
    export default {
      data() {
        var checkaccount = (rule, value, callback) => {
          if (!value) {
            return callback(new Error('账号不能为空'));
          }
          callback();
        };
        var validatePass = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('请输入密码'));
          } else {
            if (this.ruleForm.checkPass !== '') {
              this.$refs.ruleForm.validateField('checkPass');
            }
            callback();
          }
        };
        var validatePass2 = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('请再次输入密码'));
          } else if (value !== this.ruleForm.pass) {
            callback(new Error('两次输入密码不一致!'));
          } else {
            callback();
          }
        };
        return {
          ruleForm: {
            pass: '',
            checkPass: '',
            account: ''
          },
          //这里的rules表示表单验证的规则
          rules: {
            pass: [
              { validator: validatePass, trigger: 'blur' }
            ],
            checkPass: [
              { validator: validatePass2, trigger: 'blur' }
            ],
            account: [
              { validator: checkaccount, trigger: 'blur' }
            ]
          }
        };
      },
      methods: {
        submitForm(formName) {
          this.$refs[formName].validate((valid) => {
            //如果用户输入的值满足验证规则就进行下面操作
            if (valid){
            //localStorage.name保存当前用户的姓名，保存时间为直至关闭浏览器之前 
              localStorage.name=this.ruleForm.account;
              let mydata=JSON.stringify({username:this.ruleForm.account,password:this.ruleForm.pass});
             //将用户输入的信息通过UserRegistration API传给后端
              axios({
                  url:"http://localhost:3000/UserRegistration",
                  method:"post",
                  data:mydata,
                  headers:{
                    'Content-Type':'application/json'
                  }
                }).then((res)=>{
                  if(res.data){
                    console.log("注册成功");
                    //注册成功后直接跳转到完成登录的界面，即首页加载completeLog组件
                      localStorage.log=0;
                      location.href="http://localhost:3000"
                  }else{
                    console.log('erro');
                  }
                })      
            } else {
              console.log('erro');
              return false;
            }
          });
        },
        resetForm(formName) {
          this.$refs[formName].resetFields();
        }
      }
    }
  </script>
  
<style scoped>
    .myenroll{width:100%;height: 100%;display: flex;justify-content:center;align-items:center}
.myEnrollAll{width:350px;height: 450px;display: flex;justify-content:center;flex-direction: column;align-items:center;background: white;}
    .passwordInput,.nameInput{width:250px}
.enroll_top{display: flex;flex-direction: column;align-items:center;height: 270px;}
.enroll_bottom{width: 250px;}
.demo-ruleForm{margin-left: -60px;}
.enrolltips{margin-bottom: 70px;}
.forget{display: flex;justify-content:space-between;width: 250px;margin-left: 140px;}
.forget a{
    text-decoration: none;
    color: rgb(182, 182, 182);
}
/* 状态二: 已经访问过的链接 */
.forget a:visited {
    text-decoration: none;
    color: rgb(182, 182, 182);
}
/* 状态三: 鼠标划过(停留)的链接(默认红色) */
.forget a:hover {
    text-decoration: none;
    color:red;
}
/* 状态四: 被点击的链接 */
.forget a:active {
    text-decoration: none;
    color: red
}
</style>